  }
}

/* Error state */
.error-message {
  text-align: center;
  padding: 2rem;
  background-color: #fff3f3;
  border: 1px solid #ffcdd2;
  border-radius: 8px;
  color: #d32f2f;
  margin: 1rem;
}

/* Mobile Responsive Styles */
@media screen and (max-width: 768px) {
  .menu-icon {
    display: block !important;
    z-index: 1000;
  }

  .menu {
    display: none;
    position: fixed;
    top: 0;
    right: -100%;
    width: 250px;
    height: 100vh;
    background-color: var(--light-color);
    flex-direction: column;
    padding-top: 65px;
    transition: 0.3s ease-in-out;
    z-index: 99;
    gap: 0;
    box-shadow: -2px 0 4px var(--shadow-color);
  }

  .menu-toggle:checked ~ .menu {
    right: 0;
    display: flex;
  }

  .menu li {
    width: 100%;
  }

  .menu a {
    width: 100%;
    min-height: 44px;
    min-width: 44px;
    padding: 1rem 2rem;
    border-bottom: 1px solid var(--background-color);
  }

  .app-bar {
    padding: 0.8rem 1rem;
  }

  .logo-text {
    font-size: 1.2rem;
  }

  .hero {
    margin-top: 56px;
    height: 600px;
  }

  .hero-title {
    font-size: 2.8rem;
    margin-bottom: 1rem;
  }

  .hero-description {
    font-size: 1rem;
    padding: 0 1rem;
    margin-top: 1rem;
  }

  .main-content {
    padding: 1rem;
  }

  .main-content h2 {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
  }

  .restaurant-list {
    grid-template-columns: 1fr;
    gap: 1rem;
    padding: 1rem;
  }

  .restaurant-item {
    margin-bottom: 1rem;
  }

  .restaurant-info {
    padding: 1rem;
  }

  .restaurant-item h2 {
    font-size: 1.2rem;
  }

  .menu a,
  .menu-icon,
  .skip-link,
  .restaurant-item {
    min-height: 44px;
    min-width: 44px;
    padding: 12px;
    margin: 4px;
  }

  .restaurant-info p {
    margin: 0.8rem 0;
  }

  .rating {
    padding: 0.4rem 0.8rem;
    font-size: 0.9rem;
  }

  .restaurant-item img {
    height: 180px;
  }

  .restaurant-item {
    animation: fadeInUp 0.4s ease forwards;
  }
}

@media screen and (min-width: 577px) and (max-width: 768px) {
  .app-bar {
    padding: 1rem 1.5rem;
  }

  .hero {
    margin-top: 60px;
    min-height: 450px;
  }

  .hero-title {
    font-size: 2.9rem;
  }

  .hero-description {
    font-size: 1.1rem;
    max-width: 800px;
    margin-top: 1rem;
  }

  .restaurant-list {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
    padding: 1.5rem;
  }

  .main-content {
    padding: 1.5rem;
  }
}

@media screen and (min-width: 769px) {
  .app-bar {
    padding: 1rem 2rem;
  }

  .hero {
    margin-top: 64px;
    min-height: 500px;
  }

  .hero-title {
    font-size: 3rem;
    margin-bottom: 1rem;
  }

  .hero-description {
    font-size: 1.2rem;
    max-width: 800px;
    margin-top: 1rem;
  }

  .restaurant-list {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    padding: 2rem;
  }

  .main-content {
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 1200px) {
  .restaurant-list {
    grid-template-columns: repeat(4, 1fr);
  }

  .main-content {
    max-width: 1400px;
  }
}

@media screen and (max-width: 768px) {
  .restaurant-detail {
    margin-top: 60px;
    padding: 1rem;
  }

  .restaurant-detail__header {
    flex-direction: column;
  }

  .restaurant-detail__image {
    width: 100%;
    height: 250px;
  }

  .restaurant-detail__menu {
    grid-template-columns: 1fr;
  }
}

@media screen and (min-width: 768px) {
  .restaurant-detail__menu {
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    gap: 5rem;
  }

  .menu-foods,
  .menu-drinks {
    flex: 1;
    max-width: 400px;
  }

  .menu-foods ul,
  .menu-drinks ul {
    grid-template-columns: 1fr;
  }
}

@media screen and (min-width: 640px) {
  .restaurants {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 1024px) {
  .restaurants {
    grid-template-columns: repeat(3, 1fr);
  }

  .content__heading {
    font-size: 2rem;
  }
}

@media screen and (max-width: 768px) {
  .like {
    width: 45px;
    height: 45px;
    font-size: 16px;
    bottom: 24px;
    right: 24px;
  }
}

@media screen and (min-width: 650px) {
  .restaurants {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (min-width: 900px) {
  .restaurants {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Media Queries untuk Responsivitas */
@media screen and (min-width: 640px) {
  .review-form,
  .review-list {
    padding: 2rem;
  }

  .review-header {
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
  }

  .review-text {
    text-align: left;
  }
}

@media screen and (min-width: 768px) {
  .review-form,
  .review-list {
    padding: 2.5rem;
  }
}

@media screen and (max-width: 576px) {
  footer {
    padding: 1rem;
  }
}
