ms: center;\n  padding: 1rem 2rem;\n  background-color: var(--light-color);\n  box-shadow: 0 2px 4px var(--shadow-color);\n  position: fixed;\n  width: 100%;\n  top: 0;\n  z-index: 1000;\n}\n\n.logo-container {\n  display: flex;\n  align-items: center;\n}\n\n.logo-text {\n  font-size: 1.5rem;\n  margin-left: 0.8rem;\n  color: var(--primary-color);\n  font-weight: bold;\n}\n\n/* Menu Toggle and Navigation */\n.menu-toggle {\n  display: none;\n}\n\n.menu-icon {\n  display: none;\n  width: 44px;\n  height: 44px;\n  padding: 12px;\n  cursor: pointer;\n  border: none;\n  background: transparent;\n  position: relative;\n  z-index: 102;\n}\n\n.menu-icon span {\n  display: block;\n  width: 24px;\n  height: 2px;\n  background-color: var(--dark-color);\n  margin: 4px 0;\n  transition: all 0.3s ease;\n}\n\n.menu-icon.active span:nth-child(1) {\n  transform: rotate(45deg) translate(5px, 5px);\n}\n\n.menu-icon.active span:nth-child(2) {\n  opacity: 0;\n}\n\n.menu-icon.active span:nth-child(3) {\n  transform: rotate(-45deg) translate(5px, -5px);\n}\n\n.menu.active {\n  right: 0;\n  display: flex;\n}\n\n.menu {\n  display: flex;\n  list-style: none;\n  gap: 2rem;\n}\n\n.menu a {\n  color: var(--text-color);\n  text-decoration: none;\n  font-weight: 500;\n  padding: 12px;\n  min-height: 44px;\n  min-width: 44px;\n  display: inline-block;\n  align-items: center;\n  transition: color 0.3s;\n}\n\n.menu a:hover {\n  color: var(--primary-color);\n}\n\n/* Hero Section */\n.hero {\n  background-size: cover;\n  margin-top: 0;\n  width: 100%;\n  height: 600px;\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  text-align: center;\n  padding: 0;\n  overflow: hidden; /* Ensures the image fills the entire section */\n}\n\n.hero::before {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background: rgba(0, 0, 0, 0.5);\n}\n\n.hero picture {\n  display: block;\n  width: 100%;\n  heig